import back from '@/components/back'
import noData from '@/components/noData'
export default {
    components: {
        back,
        noData
    },
    data(){
        return{
            MedicalTempId: this.$route.query.MedicalTempId,
            ImageId: this.$route.query.ImageId,
            PatientName: this.$route.query.PatientName,
            Advice: '',
            Diagnosis: '',
            DiseaseDesc: '',
            OperationName: '',
            bl: [],
            cf: [],
            bg: [],
            qt: [],
            hj: [],
            CT: [],
            blC: [],
            cfC: [],
            bgC: [],
            qtC: [],
            hjC: [],
            CTC: [],
            pageShow: false,
            OperationTime:null,
        }
    },
    created(){
        this.pull()
    },
    methods: {
        pull(){
            this.$get('/PlatFormAPI/PatientArchives/GetReportImageByImageId',{
                ImageId: this.ImageId,
                MedicalTempId: this.MedicalTempId
            }).then(o => {
                this.PatientName = o.PatientName
                //如果是pdf
                if(o && o.ImgDetails && o.ImgDetails[0].FileType == 1){
            		//	文件类型(0-图片，1-pdf)
            		/*this.$store.dispatch('pushPdfUrl', o.ImgDetails[0].ImagePaths[0])
            		 * sessionStorage.setItem(`${localStorage.appid}_pdf_url`, o.ImgDetails[0].ImagePaths[0])
        			this.$router.replace("/show-pdf")*/
        			/*this.showPdf = true
        			let loadingTask = o.ImgDetails[0].ImagePaths[0]
        			
        			this.pdfUrl = pdf.createLoadingTask(loadingTask);
					this.pdfUrl.then(pdf => {
						this.numPages = pdf.numPages;
						//console.log(this.numPages)
					});*/
					
					//通知App打开pdf报告
					let _url = o.ImgDetails[0].ImagePaths[0]
					let obj = {
						title : o.DiseaseDesc,
						linkUrl : _url
					}
					this.$router.back()
					this.$bridge.callhandler('showPdfReport', JSON.stringify(obj))
        			return false;
            	}
                this.pageShow = true
                this.Advice = o.Advice
                this.Diagnosis = o.Diagnosis
                this.DiseaseDesc = o.DiseaseDesc
                this.Diagnosis = o.Diagnosis
                this.OperationName = o.OperationName
                this.OperationTime = o.OperationTime
                o.ImgDetails.forEach(v => {
                    if(this.MedicalTempId == 1){
                        if(v.ImageType === 1){
                            this.bl = v.ImagePaths
                            v.ImagePaths.forEach(a => {
                                let thumb = a.split('/')
                                thumb[thumb.length-1] = `Thumbnail/${thumb[thumb.length-1]}`
                                this.blC.push(thumb.join('/')) 
                            })
                        }  
                        else if(v.ImageType === 2){
                            this.cf = v.ImagePaths
                            v.ImagePaths.forEach(a => {
                                let thumb = a.split('/')
                                thumb[thumb.length-1] = `Thumbnail/${thumb[thumb.length-1]}`
                                this.cfC.push(thumb.join('/')) 
                            })
                        }  
                        else if(v.ImageType === 3){
                            this.bg = v.ImagePaths
                            v.ImagePaths.forEach(a => {
                                let thumb = a.split('/')
                                thumb[thumb.length-1] = `Thumbnail/${thumb[thumb.length-1]}`
                                this.bgC.push(thumb.join('/')) 
                            })
                        }  
                        else{
                            this.qt = v.ImagePaths
                            v.ImagePaths.forEach(a => {
                                let thumb = a.split('/')
                                thumb[thumb.length-1] = `Thumbnail/${thumb[thumb.length-1]}`
                                this.qtC.push(thumb.join('/')) 
                            })
                        } 
                    }
                    else if(this.MedicalTempId == 2){
                        if(v.PictureLabel == '喉镜'){
                            this.hj = v.ImagePaths
                            v.ImagePaths.forEach(a => {
                                let thumb = a.split('/')
                                thumb[thumb.length-1] = `Thumbnail/${thumb[thumb.length-1]}`
                                this.hjC.push(thumb.join('/')) 
                            })
                        }
                        else if(v.PictureLabel == 'CT、MR、DR'){
                            this.CT = v.ImagePaths
                            v.ImagePaths.forEach(a => {
                                let thumb = a.split('/')
                                thumb[thumb.length-1] = `Thumbnail/${thumb[thumb.length-1]}`
                                this.CTC.push(thumb.join('/')) 
                            })
                        }
                    }
                })
            })
        },
        toPreview(arr,idx){
            const data = {
                position: idx,
                url: arr
            }
            this.$bridge.callhandler('Preview', JSON.stringify(data))
        }
    }
}



